import { Component, OnInit } from '@angular/core';
import { FormBuilder,FormGroupDirective,NgForm, FormControl, FormGroup, Validators } from '@angular/forms';
import { Client } from 'src/app/models/client';
// import csc from 'country-state-city';
import { AuthWedService } from 'src/app/services/auth.service';
import { element } from 'protractor';
const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

@Component({
  selector: 'app-weddingdetails',
  templateUrl: './weddingdetails.component.html',
  styleUrls: ['./weddingdetails.component.css']
})
export class WeddingdetailsComponent implements OnInit {
  clientprofileForm: FormGroup;
  cities:any;
  locations:Array<any>= [];
  positionFlag = 0;
  // locationSel:any;
  locationsAll:Array<any>= [];
  email = new FormControl('',[Validators.required]);
  brideName = new FormControl('',);
  groomName = new FormControl('',);
  weddingCity = new FormControl('',[
    Validators.required
  ]);
  clientLocation = new FormControl('',[
    Validators.required
  ]);
  weddingDate = new FormControl('',[Validators.required])
  position = new FormControl('' ,[Validators.required]);
  phone = new FormControl('',[Validators.required,
    Validators.minLength(10),
    Validators.maxLength(10)]);
  fullname = new FormControl('', [Validators.required,Validators.minLength(3)]);
  currentClient :Client;
  constructor(private formBuilder: FormBuilder,private auth: AuthWedService) { 
    // this.cities = csc.getStatesOfCountry('101');
    this.auth.getCities().subscribe((res)=>{
      this.cities = res
    })
    this.auth.getLocations().subscribe((res)=>{
      // this.locations = res;
      this.locationsAll = Object.keys(res).map(i => res[i]);
    })
    this.currentClient = JSON.parse(localStorage.getItem('current_client'));
  }

  ngOnInit() {
    this.email.setValue(this.currentClient.email);
    this.phone.setValue(this.currentClient.phone);
    this.fullname.setValue(this.currentClient.fullname);
    this.brideName.setValue(this.currentClient.brideName);
    this.groomName.setValue(this.currentClient.groomName);
    this.weddingCity.setValue(this.currentClient.weddingCity);
    this.position.setValue(this.currentClient.position);
    this.weddingDate.setValue(this.currentClient.weddingDate);
    // this.onPositionChange(null);
    this.onPositionChange(this.currentClient.position);
    this.onChangeCity(this.currentClient.position);
    // this.locationSel = this.currentClient.weddingCity;
    this.clientprofileForm = this.formBuilder.group({
      _id:this.currentClient._id,
      email: this.email,
      brideName:this.brideName,
      groomName:this.groomName,
      weddingCity:this.weddingCity,
      position:this.position,
      phone:this.phone,
      fullname:this.fullname,
      weddingDate: this.weddingDate,
      clientLocation:this.clientLocation
    });
    this.onValueChanges();
  }
  submit() {
    this.auth.updateClientProile(this.clientprofileForm.value).subscribe((res)=>{
      this.auth.showSwal('success-message','Success', "saved all details");
      console.log('update');
      console.log(res);
    });
    // this.auth.signIn(this.clientprofileForm.value);
    // console.log('login');
  } 
  onPositionChange(val){
    console.log('positionchange')
    this.positionFlag = 0;
    if(val == 'groom')
      this.positionFlag = 1;
    else if(val== 'bride')
      this.positionFlag = 2;
    else if (val== 'friend/family')
      this.positionFlag = 3;
  }
  onChangeCity(val){
    console.log('on change city');
    // this.locationSel = val;
    // this.locations = this.locationsAll;
    this.locations = [];
    console.log(val)
    // console.log(this.locationsAll)
    // this.locations = this.locationsAll.filter(element => element['cityId']['value'] == val)
    // this.locations = this.locationsAll.find(element => element['cityId']['value'] == val)
    
    this.locationsAll.forEach(element => {
      if(element.cityId.value == val)
        this.locations.push(element)
    });
    console.log(this.locations)
  }
  onValueChanges(){
    // this.clientprofileForm.valueChanges.subscribe(val=>{
    //   console.log(val);
    // })
    this.clientprofileForm.get('position').valueChanges.subscribe(val => {
      this.onPositionChange(val);
    });
    this.clientprofileForm.get('weddingCity').valueChanges.subscribe(val => {

      this.onChangeCity(val);
    });
  }
}
